import Cookies from 'js-cookie';
import axios from 'axios';
import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL
} from './types'

// it matters what order these variables are.
export const register = (username, password, email, confirmation) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }


    const body = JSON.stringify({ username, password, email, confirmation })

    try {
        const res = await axios.post(`http://localhost:8000/network/register`, body)
        if (res.data.error) {
            console.log(res.data.error)
            dispatch({
                type: REGISTER_FAIL
            })
        } else {
            dispatch({
                type: REGISTER_SUCCESS
            })
        }

    } catch (err) {
        dispatch({
            type: REGISTER_FAIL
        })

    }
}

